@import "./../../info-page.scss";

.article-page {
  .body-contacts {
    background-color: #fbfbfb;
  }
  .footer {
    padding-top: 53px;
    @media (max-width: $tablet) {
      padding-top: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
    }
  }
  .current-page-info {
    &__container {
      .current-page-info__page-name {
        color: $gray;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }

  .article-section {
    padding-top: 110px;
    padding-bottom: 90px;
    @media (max-width: $tablet) {
      padding-top: 85px;
      padding-bottom: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .title-block-article {
      &__title {
        @include rale-700-32;
        max-width: 468px;
        line-height: 36px;
        @media (max-width: $mobile) {
          margin-top: 20px;
          font-size: 27px;
        }
      }
      &__date {
        margin-top: 10px;
        @include mont-500-15;
        color: $gray;
      }
    }
    .article-swiper {
      max-width: 220px;
      margin-top: 18px;
      max-height: 875px;
      height: 100%;
      margin-left: auto;
      margin-right: auto;
      @media (max-width: $tablet) {
        max-width: none;
        max-height: none;
        min-width: 0;
        overflow: hidden;
      }
      .article-wrapper{
        align-items: center;
      }
      .article__slide {
        max-width: 220px;
        max-height: 288px;
        width: 100%;
        height: 100%;
        @media (max-width: $tablet) {
          max-width: none;
          max-height: none;
        }
      }
    }
    .main-article {
      display: flex;
      justify-content: space-between;
      gap: 25px;
      overflow: hidden;
      @media (max-width: $tablet) {
        flex-direction: column;
      }
      .sidebar-main-article {
        max-width: 260px;
        @media (max-width: $tablet) {
          max-width: none;
        }
        &__title {
          margin-top: 50px;
          text-align: center;
          @include rale-700-24;
          line-height: 26px;
        }
        &__publications-block {
          margin-top: 19px;

          gap: 40px;
          @media (max-width: $tablet) {
            margin-top: 30px;
            flex-direction: row;
            justify-content: center;
          }
        }
        .footer-sidebar-main-article {
          margin-top: 72px;
          max-width: 260px;
          width: 100%;
          padding: 43px 20px 29px;
          display: flex;
          flex-direction: column;
          align-items: center;
          background-color: $darkGreen;
          @media (max-width: $tablet) {
            margin-top: 30px;
            max-width: none;
          }
          &__title {
            @include rale-800-20;
            line-height: 25px;
            text-align: center;
            max-width: 220px;
            color: $white;
          }
          &__text {
            margin-top: 15px;
            @include rale-600-17;
            color: #efefef;
            text-align: center;
            max-width: 155px;
          }
          .social {
            margin-top: 30px;
            .icon-facebook {
              color: $white;
            }
            .icon-instagram {
              color: $white;
            }
            .icon-youtube {
              color: $white;
            }
          }
        }
        .publication-sidebar-main-article {
          &__img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            max-width: 220px;
            max-height: 163px;
            width: 100%;
            height: 100%;
            @media (max-width: $tablet) {
              max-width: none;
              max-height: none;
            }
          }
          &__date {
            margin-top: 16px;
            @include mont-500-13;
          }
          &__title {
            @include rale-700-15;
            margin-top: 5px;
          }
          &__link {
            display: block;
            margin-top: 7px;
            &:hover {
            }
            &:active {
            }
            &:focus {
            }
          }
        }
      }
      .content-main-article {
        max-width: 990px;
        .list-content-main-article {
          margin-bottom: 30px;
          &__item {
            position: relative;
            margin-left: 1px;
            padding-left: 29px;
            @include rale-500-16;
            line-height: 27px;
            &:before {
              position: absolute;
              left: 0;
              content: "";
              background-color: $darkGreen;
              border-radius: 50%;
              width: 3px;
              height: 3px;
              left: 0;
              top: 50%;
              transform: translate(0, -50%);
            }
          }
        }
        .img-block-content-main-article {
          &__img {
            margin-bottom: 5px;
            margin-top: 27px;
          }
        }
        &__title {
          @include rale-700-24;
          line-height: 29px;
          margin-top: 25px;
          max-width: 500px;
          margin-bottom: 22px;
        }
        &__list-title {
          @include rale-700-16;
          margin-top: 24px;
          margin-bottom: 13px;
        }
        &__text {
          margin-top: 14px;
          @include rale-500-16;
          line-height: 25px;
          letter-spacing: 0.01em;
        }
      }
    }
  }
}
